.ex-carousel li {
  width: 20px !important;
  opacity: 1 !important;
}

.ex-carousel .carousel-indicators .active {
  background-color: #5599D3;
}

.ex-carousel .carousel-inner {
  border-radius: 0.5rem 0rem 0rem 0.5rem !important;
}

.ex-carousel .carousel-indicators {
  margin-right: 40% !important;
}

.ex-carousel ol {
  margin-bottom: 0 !important;
}

.ex-carousel .carousel-control-next, 
.ex-carousel .carousel-control-prev {
  top: 43% !important;
  bottom: 43% !important;
  width: 3% !important;
  opacity: 1 !important;
  background: rgba(0, 0, 0, 0.4);
  z-index: 999;
}

.carousel-control-prev-icon {
  background-image: url("../../icons/arrow-left.svg") !important;
}

.carousel-control-next-icon {
  background-image: url("../../icons/arrow-right.svg") !important;
}

@media only screen and (max-width: 768px) {
  .ex-carousel .carousel-inner {
    border-radius: 0.5rem !important;
  }

  .ex-carousel .carousel-indicators {
    margin-right: 15% !important;
  }
}
